<template>
  <div class="data">
    <div class="form">
      <h3 style="text-align: center">数据选择</h3>
      <br />
      <el-form class="select">
        <el-form-item label="类型">
          <el-select v-model="type">
            <el-option label="排 五" value="pl5"></el-option>
            <el-option label="七 星" value="qxc"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="年份">
          <el-select v-model="year">
            <el-option label="全 部" value=""></el-option>
            <el-option
              v-for="i in 4"
              :key="i"
              :label="2023 - i + 1"
              :value="2023 - i + 1"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="千位">
          <el-cascader
            clearable
            :show-all-levels="false"
            filterable
            :props="{ expandTrigger: 'hover' }"
            :options="options"
            v-model="thousands"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="百位">
          <el-cascader
            clearable
            :show-all-levels="false"
            filterable
            :props="{ expandTrigger: 'hover' }"
            :options="options"
            v-model="hundreds"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="十位">
          <el-cascader
            clearable
            :show-all-levels="false"
            filterable
            :props="{ expandTrigger: 'hover' }"
            :options="options"
            v-model="tens"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="个位">
          <el-cascader
            :show-all-levels="false"
            clearable
            filterable
            :props="{ expandTrigger: 'hover' }"
            :options="options"
            v-model="units"
          >
          </el-cascader>
        </el-form-item>
      </el-form>
      <Echarts2 :data="chartData.pieData" />
    </div>
    <div class="res">
      <h3>统计结果</h3>
      <br />

      <el-row class="sec">
        <el-col :span="6">
          <span class="title">最大遗漏次数</span><br />
          <span class="num">{{ miss.max }}</span>
        </el-col>

        <el-col :span="6">
          <span class="title">最后遗漏次数</span><br />
          <span class="num">{{ miss.last }}</span> </el-col
        ><el-col :span="6">
          <span class="title">最大遗漏期号</span><br />
          <span class="num">{{ miss.num }}</span></el-col
        >
        <el-col :span="6">
          <span class="title">平均遗漏次数</span>
          <br />
          <span class="num">{{ miss.average }}</span>
        </el-col>
      </el-row>

      <Echarts :data="chartData.lineData" />

      <div class="arr">
        <h4 style="text-align: center">遗漏数组 ({{ miss.arr.length }})</h4>
        {{ miss.arr }}
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import Echarts from "./Echarts.vue";
  import Echarts2 from "./Echarts2.vue";

  export default {
    name: "Static",
    components: { Echarts, Echarts2 },
    data() {
      return {
        type: "pl5",
        year: "2023",
        thousands: "",
        hundreds: "",
        tens: "",
        units: "",
        options: [
          {
            value: "1",
            label: "性质",
            children: [
              { label: "奇【13579】", value: "j" },
              { label: "偶【02468】", value: "o" },
              { label: "质【12357】", value: "z" },
              { label: "合【04689】", value: "h" },
              { label: "大【01234】", value: "d" },
              { label: "小【56789】", value: "x" },
            ],
          },
          {
            value: "2",
            label: "6 位",
            children: [
              { label: "0-5", value: "0-5" },
              { label: "1-6", value: "1-6" },
              { label: "2-7", value: "2-7" },
              { label: "3-8", value: "3-8" },
              { label: "4-9", value: "4-9" },
              { label: "901456", value: "$901456" },
            ],
          },

          {
            value: "3",
            label: "路数",
            children: [
              { label: "0 【0369】", value: "l0" },
              { label: "1 【1 4 7】", value: "l1" },
              { label: "2 【2 5 8】", value: "l2" },
              { label: "大【7 8 9】", value: "7-9" },
              { label: "中【3456】", value: "3-6" },
              { label: "小【0 1 2】", value: "0-2" },
            ],
          },
          {
            value: "3.1",
            label: "对数",
            children: [
              { label: "05", value: "05" },
              { label: "16", value: "16" },
              { label: "27", value: "27" },
              { label: "38", value: "38" },
              { label: "49", value: "49" },
            ],
          },
          {
            value: "4",
            label: "3 位",
            children: [
              { label: "0-2", value: "0-2" },
              { label: "1-3", value: "1-3" },
              { label: "2-4", value: "2-4" },
              { label: "3-5", value: "3-5" },
              { label: "4-6", value: "4-6" },
              { label: "5-7", value: "5-7" },
              { label: "6-8", value: "6-8" },
              { label: "7-9", value: "7-9" },
            ],
          },

          {
            value: "5",
            label: "4 位",
            children: [
              { label: "0-3", value: "0-3" },
              { label: "1-4", value: "1-4" },
              { label: "2-5", value: "2-5" },
              { label: "3-6", value: "3-6" },
              { label: "4-7", value: "4-7" },
              { label: "5-8", value: "5-8" },
              { label: "6-9", value: "6-9" },
            ],
          },
          {
            value: "6",
            label: "5 位",
            children: [
              { label: "0-4", value: "0-4" },
              { label: "1-5", value: "1-5" },
              { label: "2-6", value: "2-6" },
              { label: "3-7", value: "3-7" },
              { label: "4-8", value: "4-8" },
              { label: "5-9", value: "6-9" },
            ],
          },
          {
            value: "7",
            label: "7 位",
            children: [
              { label: "0-6", value: "0-6" },
              { label: "1-7", value: "1-7" },
              { label: "2-8", value: "2-8" },
              { label: "3-9", value: "3-9" },
            ],
          },
          {
            value: "8",
            label: "8 位",
            children: [
              { label: "0-7", value: "0-7" },
              { label: "1-8", value: "1-8" },
              { label: "2-9", value: "2-9" },
            ],
          },
        ],
      };
    },
    computed: {
      ...mapGetters(["pl5", "qxc"]),

      chartData() {
        let map = new Map();

        this.miss.arr.forEach((v) => {
          map.set(v, (map.get(v) || 0) + 1);
        });

        let keys = [...map.keys()].sort((a, b) => a - b),
          lineData = { xAxis: [], yAxis: [] },
          pieData = [];
        for (let key of keys) {
          let v = map.get(key);
          lineData.xAxis.push(key);
          lineData.yAxis.push(v);
          pieData.push({ value: v, name: key });
        }
        return { lineData, pieData };
      },

      miss() {
        let [q, b, s, g] = [
          this.thousands[1],
          this.hundreds[1],
          this.tens[1],
          this.units[1],
        ];

        console.log([q, b, s, g]);

        const func = {
          j: (n) => n % 2 === 1,
          o: (n) => n % 2 === 0,

          d: (n) => n >= 5,
          x: (n) => n < 5,

          z: (n) => [1, 2, 3, 5, 7].indexOf(n) != -1,
          h: (n) => [1, 2, 3, 5, 7].indexOf(n) === -1,

          l0: (n) => n % 3 === 0,
          l1: (n) => n % 3 === 1,
          l2: (n) => n % 3 === 2,

          "05": (n) => n == 0 || n == 5,
          16: (n) => n == 1 || n == 6,
          27: (n) => n == 2 || n == 7,
          38: (n) => n == 3 || n == 8,
          49: (n) => n == 4 || n == 9,

          "7-9": (n) => n >= 7,
          "3-6": (n) => n <= 6 && n >= 3,
          "0-2": (n) => n <= 2,

          901456: (n) => [9, 0, 1, 4, 5, 6].indexOf(n) != -1,
          /*
            // 6位
            "0-5": (n) => n <= 5 && n >= 0,
            "1-6": (n) => n <= 6 && n >= 1,
            "2-7": (n) => n <= 7 && n >= 2,
            "3-8": (n) => n <= 8 && n >= 3,
            "4-9": (n) => n <= 9 && n >= 4,
          */

          // 添加属性, N位判断
          g(N) {
            for (let i = 0; i <= 9 - N + 1; i++) {
              let attributeName = `${i}-${i + N - 1}`;
              this[attributeName] = (n) => n >= i && n <= i + N - 1;
            }
          },
        };

        // 3 4 5 6 7 8位
        func.g(3), func.g(4), func.g(5), func.g(6), func.g(7), func.g(8);

        // 数据 // 遗漏计数 // 最大遗漏次数 // 最大遗漏对应期数 // 遗漏数组
        let [data, m, max, num, last, arr] = [
          this[this.type],
          0,
          0,
          "23001",
          0,
          [],
        ];

        let i = 0;
        for (; i < data.length; i++) {
          let res = data[i].result;

          // 条件计算
          let [l1, l2, l3, l4] = [
            q ? func[q](parseInt(res[0])) : 1,
            b ? func[b](parseInt(res[1])) : 1,
            s ? func[s](parseInt(res[2])) : 1,
            g ? func[g](parseInt(res[3])) : 1,
          ];

          // 巧妙的设计
          if (l1 && l2 && l3 && l4) {
            max < m && ([max, num] = [m, data[i - 1].num]);
            arr.push(m);
            m = 0;
          } else {
            m++;
          }
        }

        // 边界处理
        if (m > 0) {
          last = m;
          max < m && ([max, num] = [m, data[i - 1].num]);
          arr.push(m);
        }

        let sum = 0,
          average = 0;
        arr.forEach((v) => {
          sum += v;
        });

        average = Math.ceil(sum / arr.length);

        return { num, max, last, arr, average };
      },
    },
    watch: {
      type: {
        immediate: true,
        handler(newVal) {
          this.$store.commit("setJson", { type: newVal, year: this.year });
        },
      },
      year: {
        immediate: true,
        handler(newVal) {
          this.$store.commit("setJson", { type: this.type, year: newVal });
        },
      },
    },
  };
</script>

<style lang="css" scoped>
  .data {
    display: flex;
    box-sizing: border-box;
    overflow: auto;
    width: 80%;
    text-align: left;
  }

  .data .form {
    padding: 30px 0 10px 0;
  }
  .select {
    height: 50%;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    overflow: auto;
  }
  .data .el-form-item {
    margin: 5px;
  }
  .res {
    /* background-color: #a8f5a4; */
    padding: 30px 10px 0px 10px;
    box-sizing: border-box;
    text-align: center;
    border-left: 2px solid rgb(232, 229, 229);
  }
  .form,
  .res {
    flex-grow: 1;
    width: 50%;
    overflow: auto;
  }

  .arr {
    overflow: auto;
    text-align: justify;
    line-height: 25px;
    height: 170px;
    padding: 0 20px;
  }

  .sec .title {
    color: #606266;
    font-size: 13px;
  }

  .sec .num {
    color: #303133;
    font-size: 20px;
  }

  .sec {
    height: 25px;
  }
</style>
