<template>
  <div class="ana">
    
    <el-form class="first" :inline="true">
      <el-form-item label="本钱">
        <el-input-number
          controls-position="right"
          v-model="base1"
          :min="1"
        ></el-input-number>
        ×
        <el-input-number
          controls-position="right"
          v-model="base2"
          :min="1"
        ></el-input-number>
        = {{ baseMoney }}
      </el-form-item>
      &nbsp; 
      <el-form-item label="赔率">
        <el-radio-group v-model="zhon">
          <el-radio-button :label="95">1:95</el-radio-button>
          <el-radio-button :label="850">1:850</el-radio-button
          ><el-radio-button :label="9500">1:9500</el-radio-button>
        </el-radio-group> </el-form-item
      >&nbsp; 
      <el-form-item label="基数">
        <el-input-number
          controls-position="right"
          v-model="a1"
          :min="0"
        ></el-input-number>
      </el-form-item>
    </el-form>
    
    <el-divider></el-divider>
    
    <el-form :inline="true">
      <el-form-item label="公比1">
        <el-input-number
          controls-position="right"
          v-model="q1"
          :step="0.1"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖)">
        <el-input-number
          controls-position="right"
          v-model="n1"
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="过渡公比 1">
        <el-input-number
          controls-position="right"
          v-model="g1"
          :step="0.05"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <br />
      基数：{{ a1 }} &nbsp; | &nbsp; 当期：{{ c1.bet }} &nbsp; | &nbsp;
      当期本钱：{{ c1.bet * baseMoney }} &nbsp; | &nbsp; 总本钱：{{
        c1.ben
      }}
      &nbsp; | &nbsp; 中奖：{{ c1.zhon }} &nbsp; | &nbsp; 赢钱：{{
        c1.zhon - c1.ben
      }}
      &nbsp; | &nbsp; 日均：{{ Math.floor((c1.zhon - c1.ben) / n1) }}
    </el-form>
    
    <el-divider></el-divider>
    
    <el-form  :inline="true">
      <el-form-item label="公比 2">
        <el-input-number
          controls-position="right"
          v-model="q2"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖)">
        <el-input-number
          controls-position="right"
          v-model="n2"
          :min="n1 + 1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="过渡公比 2">
        <el-input-number
          controls-position="right"
          v-model="g2"
          :step="0.05"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <br />
      基数：{{ a2 }} &nbsp; | &nbsp; 当期：{{ c2.bet }} &nbsp; | &nbsp;
      当期本钱：{{ c2.bet * baseMoney }} &nbsp; | &nbsp; 总本钱：{{
        c2.ben + c1.ben
      }}
      &nbsp; | &nbsp; 中奖：{{ c2.zhon }} &nbsp; | &nbsp; 赢钱：{{
        c2.zhon - c1.ben - c2.ben
      }}
      &nbsp; | &nbsp; 日均：{{ Math.floor((c2.zhon - c1.ben - c2.ben) / n2) }}
    </el-form>
    
    <el-divider></el-divider>
    
    <el-form  :inline="true">
      <el-form-item label="公比 3">
        <el-input-number
          controls-position="right"
          v-model="q3"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖)">
        <el-input-number
          controls-position="right"
          v-model="n3"
          :min="n2 + 1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="过渡公比 3">
        <el-input-number
          controls-position="right"
          v-model="g3"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><br />
      基数：{{ a3 }} &nbsp; | &nbsp; 当期：{{ c3.bet }} &nbsp; | &nbsp;
      当期本钱：{{ c3.bet * baseMoney }} &nbsp; | &nbsp; 总本钱：{{
        c3.ben + c2.ben + c1.ben
      }}
      &nbsp; | &nbsp; 中奖：{{ c3.zhon }} &nbsp; | &nbsp; 赢钱：{{
        c3.zhon - c1.ben - c2.ben - c3.ben
      }}
      &nbsp; | &nbsp; 日均：{{
        Math.floor((c3.zhon - c1.ben - c2.ben - c3.ben) / n3)
      }}
    </el-form>
    
    <el-divider></el-divider>
    
    <el-form :inline="true">
      <el-form-item label="公比 4">
        <el-input-number
          controls-position="right"
          v-model="q4"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖)">
        <el-input-number
          controls-position="right"
          v-model="n4"
          :min="n3 + 1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="过渡公比 2">
        <el-input-number
          controls-position="right"
          v-model="g4"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><br />
      基数：{{ a4 }} &nbsp; | &nbsp; 当期：{{ c4.bet }} &nbsp; | &nbsp;
      当期本钱：{{ c4.bet * baseMoney }} &nbsp; | &nbsp; 总本钱：{{
        c4.ben + c3.ben + c2.ben + c1.ben
      }}
      &nbsp; | &nbsp; 中奖：{{ c4.zhon }} &nbsp; | &nbsp; 赢钱：{{
        c4.zhon - c1.ben - c2.ben - c3.ben - c4.ben
      }}
      &nbsp; | &nbsp; 日均：{{
        Math.floor((c4.zhon - c1.ben - c2.ben - c3.ben - c4.ben) / n4)
      }}
    </el-form>

    
  </div>
</template>

<script>
  export default {
    name: "Win",

    data() {
      return {
        base1: 6,
        base2: 6,
        zhon: 95,

        a1: 5,
        q1: 2,
        n1: 10,
        g1: 1.9,

        q2: 1.8,
        n2: 1,
        g2: 1.5,

        q3: 1.5,
        n3: 1,
        g3: 1.4,

        q4: 1.4,
        n4: 1,
        g4: 1.4,
      };
    },

    computed: {
      baseMoney() {
        return this.base1 * this.base2;
      },
      c1() {
        return this.calc(this.a1, this.q1, this.n1);
      },

      a2() {
        return Math.ceil(this.c1.bet * this.g1);
      },

      c2() {
        return this.calc(this.a2, this.q2, this.n2 - this.n1);
      },

      a3() {
        return Math.ceil(this.c2.bet * this.g2);
      },

      c3() {
        return this.calc(this.a3, this.q3, this.n3 - this.n2);
      },

      a4() {
        return Math.ceil(this.c3.bet * this.g3);
      },

      c4() {
        return this.calc(this.a4, this.q4, this.n4 - this.n3);
      },

      // total() {
      //   let sum = 0;
      //   let n = this.nStr.split(" ");
      //   console.log(n);
      //   for (let i = 0; i < n.length; i++) {
      //     let num = parseInt(n[i]);
      //     if (!num) continue;
      //     sum =
      //       sum +
      //       (Math.pow(this.q1, num - 1) * this.zhon * this.a1 -
      //         ((Math.pow(this.q1, num) - 1) / (this.q1 - 1)) *
      //           this.baseMoney *
      //           this.a1);
      //   }
      //   return sum;
      // },
    },
    watch: {
      n1: {
        immediate: true,
        handler(newVal) {
          if (newVal >= this.n2) {
            this.n2 = newVal + 1;
          }
        },
      },
    },

    methods: {
      // 计算
      calc(a, q, n) {
        let bet = Math.ceil(Math.pow(q, n - 1) * a); //当期应打

        let zhon = bet * this.zhon; //当期中奖

        let ben = //当期本钱
          q != 1
            ? Math.ceil(((Math.pow(q, n) - 1) / (q - 1)) * this.baseMoney * a)
            : n * this.baseMoney * a;

        return {
          bet,
          zhon,
          ben,
        };
      },
    },
  };
</script>

<style lang="css" scoped>
  .ana {
    text-align: center;
  }

  .first .el-form-item{
    margin: 0;
  }

  .first{
    padding: 30px 0 6px 0;
  }

  
</style>
