          }]
        }]
      };
    },
    computed: {
      map() {
        let arr = [9, "", 9];
        return arr.indexOf("") + "" + arr.lastIndexOf("");
      },
    },

  methods: {
      handleChange(value) {
        console.log(value);
      },
      //实时修改input值
      //实时修改输入框内容
      setValue() {
        let str = this.haoMa;

        // - ，任意字母 => X
        /\-|[A-z]/g.test(str) && (this.haoMa = str.replace(/\-|[A-z]/g, "X"));

        // + => XX
        /\+/g.test(str) && (this.haoMa = str.replace(/\+/g, "XX"));

        // 空格，/ => :
        /\s|\/|\./g.test(str) && (this.haoMa = str.replace(/\s|\/|\./g, ":"));
      },

      change() {
        this.obj.push(0);
        this.num[0] = 9;
        this.sum[0] = 8;
        this.list[0].name = "ooo";
        console.log(this.obj, this.num, this.sum);
      },
      change2() {
        this.obj.push(0);
        let num = this.num;
        num[0] = 1000;
      },

      d() {
        console.log("组件被创建了");
      },
      getTime(date) {
        let month = zeroFill(date.getMonth() + 1); //月
        let day = zeroFill(date.getDate()); //日
        let hour = zeroFill(date.getHours()); //时
        let minute = zeroFill(date.getMinutes()); //分
        let second = zeroFill(date.getSeconds()); //秒

        //年月日
        let hms = hour + ":" + minute + ":" + second;

        let ymd = date.getFullYear() + "-" + month + "-" + day;

        return { ymd: ymd, hms: hms };

        /**
         * 补零
         */
        function zeroFill(time) {
          if (time >= 0 && time <= 9) {
            return "0" + time;
          } else {
            return time;
          }
        }
      },

      test() {
        let obj = {
          "0-6": '我是obj对象的属性"0-6"',
          "3-8"() {
            console.log("我是方法'3-8', ", this["0-6"]);
          },
        };

        obj["3-8"]();
      },
    },
    mounted() {
      let str = "1XX6";
      let arr = str.split(/[A-z]/);
      console.log(arr, parseInt(str), "1" > 1);

      let arr2 = [2, 3, 5, 6, 78, 8];
      let arr3 = arr2.filter((num) => num > 5);
      console.log(arr3);

      let reg =
        //       1XX8              X1X8                     1X8X                xx18                 18xx              X18X
        /^[0-9]{1}XX[0-9]{1}$|^X[0-9]{1}X[0-9]{1}$|^[0-9]{1}X[0-9]{1}X$|^XX[0-9]{1}[0-9]{1}$|^[0-9]{1}[0-9]{1}XX$|^X[0-9]{1}[0-9]{1}X$/i;
      console.log("reg1匹配：", reg.test("xx16 "));

      let reg2 = /^[0-9]+$|^(\s){0}$/g;

      console.log(reg2.test("786"));
    },
  };
</script>

<style scoped>
  .test {
    text-align: center;
