//在Echarts.vue文件中
<template>
  <div>
    <div id="main" style="width: 100%; height: 350px"></div>
  </div>
</template>

<script>
  export default {
    name: "Echarts",

    props: ["data"],

    watch: {
      data: {
        handler() {
          this.myEcharts();
        },
      },
    },

    methods: {
      myEcharts() {
        var myChart = this.$echarts.init(document.getElementById("main"));

        //配置图表
        var option = {
          tooltip: {
            trigger: "axis",
            triggerOn: "mousemove",
            enterable: true, //鼠标是否可进入提示框浮层中
            formatter: "遗漏 : {b}，频数 : {c}", //修改鼠标悬停显示的内容
           
          },
          // legend: {},
          // legend: {
          //   data: ['销量']
          // },
          xAxis: {
            data: this.data.xAxis,
          },
          yAxis: {
            splitLine: {
              //网格线
              lineStyle: {
                type: "dashed", //设置网格线类型 dotted：虚线   solid:实线
              },
              show: true, //隐藏或显示
            },
          },
          series: [
            {
              name: "遗漏次数 | 频率",
              type: "line",

              data: this.data.yAxis,
              smooth: true,
              itemStyle: {
                normal: {
                  label: {
                    show: true, //开启显示
                    position: "top", //在上方显示
                    //  distance:0,
                    textStyle: {
                      //数值样式
                      color: "#000",
                      fontSize: 12,
                      // fontWeight:500
                    },
                  },
                },
              },
            },
          ],
        };
        myChart.setOption(option);
      },
    },
    mounted() {
      this.myEcharts();
    },
  };
</script>

