//引入VueRouter
import VueRouter from "vue-router";
//引入组件
import ComNum from "../components/ComNum";
import QuickHit from "../components/QuickHit";
import Index from "../components/Index";
import Tip from "../components/other/Tip";
import System from "../components/other/System";
import Test from "../components/other/Test";
import Data from "../components/data/Data";
import Analysis from "@/components/Analysis";
import Static from '@/components/Static'

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
  mode: "hash",
  routes: [
    {
      path: "/comnum",
      component: ComNum,
    },
    {
      path: "/quickhit",
      component: QuickHit,
    },
    {
      path: "/",
      component: Index,
    },
    {
      path: "/tip",
      component: Tip,
    },
    {
      path: "/system",
      component: System,
    },
    {
      path: "/test",
      component: Test,
    },
    {
      path: "/data",
      component: Data,
    },
    {
      path: "/analysis",
      component: Analysis,
    },
    {
      path: "/static",
      component: Static,
    },
  ],
});

//暴露router
export default router;
