//在Echarts.vue文件中
<template>
  <div>
    <div id="main2" style="width: 100%; height: 245px"></div>
  </div>
</template>

<script>
  export default {
    name: "Echarts",

    props: ["data"],

    watch: {
      data: {
        handler() {
          this.myEcharts();
        },
      },
    },

    methods: {
      myEcharts() {
        var myChart = this.$echarts.init(document.getElementById("main2"));
        //配置图表

        const option = {
          color: [
            // 新版颜色
            "#5470c6",
            "#91cc75",
            "#fac858",
            "#ee6666",
            "#73c0de",
            "#3ba272",
            "#fc8452",
            "#9a60b4",
            "#ea7ccc",
            // 旧版颜色
            "#c23531",
            "#2f4554",
            "#61a0a8",
            "#d48265",
            "#91c7ae",
            "#749f83",
            "#ca8622",
            "#bda29a",
            "#6e7074",
            "#546570",
            "#c4ccd3",
          ],
          tooltip: {
            trigger: "item",
            triggerOn: "mousemove",
            enterable: true, //鼠标是否可进入提示框浮层中
            formatter: "频数 : {c}", //修改鼠标悬停显示的内容
          },
          // title: {
          //   text: "遗漏次数占比",
          //   left: "left",
          //   top: "top",
          // },
          series: [
            {
              type: "pie",
              data: this.data,
              label: {
                normal: {
                  formatter: "{b}次，{d}%",
                },
              },
            }, //dd
          ],
        };
        myChart.setOption(option);
      },
    },
    mounted() {
      // console.log(this.axisData);
      this.myEcharts();
    },
  };
</script>

<style>
  /* #main2{
  margin-top: 2px;
  
} */
</style>
